import React from "react";
import styles from "./Footer.module.css";
import commonStyles from "../../styles/commonStyles.module.css";
import Title from "../../components/title/Title";
import linkedIn from "../../img/footer/linkedin.svg"
import instagram from "../../img/footer/instagram.svg"
import github from "../../img/footer/github.svg"
import email from "../../img/footer/email.svg"
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

class Footer extends React.Component {
    private icon: any

    componentDidMount() {
        gsap.registerPlugin(ScrollTrigger)
        gsap.from(".Footer_element__3Thvi",
            {
                scrollTrigger: {
                    trigger: this.icon,
                    start: "bottom bottom"
                },
                opacity: 0,
                y: 90,
                ease: "elastic.out(1.2, 0.3)",
                duration: 2.7,
                stagger: 0.2
            });
    }

    render() {
        return (
            <footer className={styles.footer}>
                <div className={`${commonStyles.container} ${styles.footerContainer}`}>

                    <Title title="Andrew Sibiriakov"/>

                    <div className={styles.elementsWrapper}>
                        <div ref={div => this.icon = div} className={styles.element}>
                            <a target="_blank" href="https://www.linkedin.com/in/andrey-sibiriakov-6a54941b2">
                                <img style={{width: "100%"}} src={linkedIn} alt="Linkedin"/>
                            </a>
                        </div>
                        <div ref={div => this.icon = div} className={styles.element}>
                            <a target="_blank" href="https://www.instagram.com/sibiryakov_bro">
                                <img style={{width: "100%"}} src={instagram} alt="Instagram"/>
                            </a>
                        </div>
                        <div ref={div => this.icon = div} className={styles.element}>
                            <a target="_blank" href="https://github.com/sibiryakov090591">
                                <img style={{width: "100%"}} src={github} alt="Github"/>
                            </a>
                        </div>
                        <div className={styles.element}>
                            <a target="_blank" href="mailto:sibir_91@list.ru">
                                <img className={styles.mail} style={{width: "100%"}} src={email} alt="Email"/>
                            </a>
                        </div>
                    </div>
                    <div>
                        Â© 2021 Copyright
                    </div>
                </div>
            </footer>
        )
    }
}

export default Footer;
