import React from "react";
import styles from "./Contacts.module.css";
import commonStyles from "../../styles/commonStyles.module.css";
import Title from "../../components/title/Title";
import Button from "../../components/button/Button";

const Contacts: React.FC = () => {
    return (
        <section id="contacts" className={styles.contacts}>
            <div className={`${commonStyles.container} ${styles.contactsContainer}`}>
                <Title title="Email"/>
                <form className={styles.form}>
                    <input disabled className={styles.input} type="text" placeholder="This"/>
                    <input disabled className={styles.input} type="text" placeholder="functionality"/>
                    <textarea disabled className={styles.textarea} placeholder="is not yet available"></textarea>
                </form>
                <Button disabled>Send message</Button>
            </div>
        </section>
    )
}

export default Contacts;
