import React from "react";
import styles from "./Skills.module.css";
import commonStyles from "../../styles/commonStyles.module.css";
import Skill from "../../components/skill/Skill";
import Title from "../../components/title/Title";
import reactIcon from "../../img/react_logo.png";
import tsIcon from "../../img/typescript_logo.png";
import htmlIcon from "../../img/html_css.png";
import apiIcon from "../../img/api_rest.png";


// GSAP Animation



// Component
const Skills: React.FC = () => {
    return (
        <section id="skills" className={styles.skills}>
            <div className={`${commonStyles.container} ${styles.skillsContainer}`}>
                <Title title="Skills"/>
                <div className={styles.skillsWrapper}>
                    <Skill iconUrl={reactIcon}>
                        I often use next technologies: <br/> React-Redux for state managing,
                        React-Router-Dom for work with routs, Redux-Form and Formik, Redux-Thunk for side-effects.
                    </Skill>
                    <Skill iconUrl={tsIcon}>
                        I like statically typed languages because there is so much more time left to code
                        than fix bugs.
                    </Skill>
                    <Skill iconUrl={htmlIcon}>
                        I definitely use sumantic HTML tags, for cross-browser compatibility i use normalize.css
                        and and check the browser support for the tags used (Hello IE!...) And I really enjoy
                        working with animation.
                    </Skill>
                    <Skill iconUrl={apiIcon}>
                        For HTTP requests (CRUD operations) I am using axios library.
                    </Skill>
                </div>
            </div>
        </section>
    )
}

export default Skills;
