import React from "react";
import styles from "./Project.module.css";
import Slider from "react-slick";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

type PropsType = {
    imgUrl: string[]
    title: string
    href: string
}

class Project extends React.Component<PropsType> {
    private project: any

    componentDidMount() {
        gsap.registerPlugin(ScrollTrigger)
        gsap.from(this.project, {
            scrollTrigger: this.project,
            y: 50,
            opacity: 0,
            duration: 1
        })
    }

    render() {

        // Slider settings
        const settings = {
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            fade: true,
            dots: false,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 3000,
            speed: 2000
        }


        // Map slides
        const slides = this.props.imgUrl.map((item, index) => {
            return (
                <div key={index.toString()} className={styles.slideWrapper}>
                    <img className={styles.slide}
                         src={item}
                         alt={`slide ${index}`}/>
                </div>
            )
        })


        // Render
        return (
            <div ref={div => this.project = div} className={styles.wrapper}>

                <a target="_blank" href={this.props.href}>
                    <Slider className={styles.slider} {...settings}>
                        {slides}
                    </Slider>
                </a>

                <div className={styles.description}>
                    <h3 className={styles.title}>
                        {this.props.title}
                    </h3>
                    <p className={styles.text}>
                        {this.props.children}
                    </p>
                </div>
            </div>
        )
    }
}

export default Project;
