import React from "react";
import styles from "./HireMe.module.css";
import commonStyles from "../../styles/commonStyles.module.css";
import Title from "../../components/title/Title";
import Button from "../../components/button/Button";
import {Popover} from '@varld/popover';
import FileSaver from 'file-saver';

const HireMe: React.FC = () => {


    //Render
    return (
        <section className={styles.hire}>
            <div className={`${commonStyles.container} ${styles.hireContainer}`}>

                <Title title="Resume"/>

                <Popover popover={({visible, open, close}) => {

                    const saveFile = (url: string, fileName: string) => {
                        close()
                        FileSaver.saveAs(process.env.PUBLIC_URL + url, fileName);
                    }

                    return (
                        <div className={styles.popupWindow}>
                            <Button className={styles.button} onClick={() => saveFile(
                                "/resume/resume_eng.pdf",
                                "resume_eng.pdf"
                            )}>
                                In english
                            </Button>
                            <Button className={styles.button} onClick={() => saveFile(
                                "/resume/resume_ru.pdf",
                                "resume_ru.pdf"
                            )}>
                                In russian
                            </Button>
                        </div>
                    )
                }}>

                    <Button>Download</Button>

                </Popover>
            </div>
        </section>
    )
}

export default HireMe;
