import React, {useState} from "react";
import styles from "./Burger.module.css";
import icon from "../../img/burger.png";
import close from "../../img/close_menu.png";
import {Link} from "react-scroll";

const Burger: React.FC = () => {

    // Hooks
    const [editMenu, setEditMenu] = useState(false)


    // Handlers
    const editMenuHandler = () => {
        setEditMenu(!editMenu)
    }
    const linkEditMenuHandler = () => {
        const clientWidth = document.documentElement.clientWidth
        if (clientWidth <= 900) {
            setEditMenu(!editMenu)
        }
    }


    // Dynamic styles
    const iconActive = `${styles.burger} ${editMenu && styles.burgerActive}`
    const menuActive = `${styles.menu} ${editMenu && styles.menuActive}`


    // Render
    return (
        <>
            {/*Burger icon*/}
            <div onClick={editMenuHandler} className={iconActive}>
                <div className={styles.burgerCircle}>
                    <img src={icon} alt="menu"/>
                </div>
                <div>

                </div>
            </div>


            {/*Menu-list*/}
            <nav className={menuActive}>
                <ul className={styles.menuList}>

                    {/*Hero link*/}
                    <Link activeClass={styles.active}
                          onClick={linkEditMenuHandler}
                          className={styles.menuItem}
                          to="hero"
                          spy={true}
                          smooth={true}
                          duration={500}
                          offset={0}
                    >
                        Home
                    </Link>

                    {/*Skills link*/}
                    <Link activeClass={styles.active}
                          onClick={linkEditMenuHandler}
                          className={styles.menuItem}
                          to="skills"
                          spy={true}
                          smooth={true}
                          duration={500}
                          offset={0}
                    >
                        Skills
                    </Link>

                    {/*Projects link*/}
                    <Link activeClass={styles.active}
                          onClick={linkEditMenuHandler}
                          className={styles.menuItem}
                          to="projects"
                          spy={true}
                          smooth={true}
                          duration={500}
                          offset={0}
                    >
                        Projects
                    </Link>

                    {/*Contacts link*/}
                    <Link activeClass={styles.active}
                          onClick={linkEditMenuHandler}
                          className={styles.menuItem}
                          to="contacts"
                          spy={true}
                          smooth={true}
                          duration={500}
                          offset={0}
                    >
                        Contact
                    </Link>
                </ul>

                {/*Close icon*/}
                <svg onClick={editMenuHandler}
                     className={styles.close}
                     version="1.1" id="Capa_1"
                     xmlns="http://www.w3.org/2000/svg"
                     x="0px"
                     y="0px"
                     width="35px"
                     height="35px"
                     viewBox="0 0 93.934 93.934"
                     fill="#ffffff">
                    <g><path d="M80.178,13.757c-18.341-18.342-48.08-18.342-66.421,0c-18.342,18.341-18.342,48.08,0,66.421
		c18.341,18.342,48.08,18.342,66.421,0C98.52,61.836,98.52,32.098,80.178,13.757z M71.576,61.737l-9.838,9.838l-14.771-14.77
		l-14.771,14.77l-9.838-9.838l14.77-14.771l-14.77-14.771l9.838-9.838l14.771,14.771l14.771-14.771l9.838,9.838l-14.77,14.772
		L71.576,61.737z"/></g>
                </svg>

            </nav>
        </>
    )
}

export default Burger;
