import React from "react";
import styles from "./Skill.module.css";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

type PropsType = {
    iconUrl: string
}

class Skill extends React.Component<PropsType> {
    private skill: any

    componentDidMount() {
        gsap.registerPlugin(ScrollTrigger)
        gsap.from(this.skill, {
            scrollTrigger: this.skill,
            y: 40,
            opacity: 0,
            duration: 1
        })
    }


    render() {
        return (
            <div ref={div => this.skill = div} className={styles.wrapper}>
                <img className={styles.icon} src={this.props.iconUrl} alt="Project icon"/>

                <p className={styles.description}>
                    {this.props.children}
                </p>
            </div>
        )
    }
}

export default Skill;
