import React from "react";
import styles from "./Projects.module.css";
import commonStyles from "../../styles/commonStyles.module.css";
import Title from "../../components/title/Title";
import Project from "../../components/project/Project";
import socialImg1 from "../../img/projects/Social/Social_1.jpg";
import socialImg2 from "../../img/projects/Social/Social_2.jpg";
import socialImg3 from "../../img/projects/Social/Social_3.jpg";
import todoImg1 from "../../img/projects/Todo/todo_1.jpg";
import todoImg2 from "../../img/projects/Todo/Screenshot_2.jpg";
import counterImg1 from "../../img/projects/Counter/counter_1.jpg";
import counterImg2 from "../../img/projects/Counter/counter_2.jpg";
import counterImg3 from "../../img/projects/Counter/counter_3.jpg";


const Projects: React.FC = () => {

    // Render
    return (
        <section id="projects" className={styles.projects}>
            <div className={`${commonStyles.container} ${styles.projectsContainer}`}>
                <Title title="Works"/>
                <div className={styles.projectsWrapper}>
                    <Project href={"https://github.com/sibiryakov090591/Social-Network"} imgUrl={[socialImg1, socialImg2, socialImg3]} title="Social network">
                        What tools were used: React.js, Hooks, Redux, React-router-dom, Redux-form, Redux-thunk,
                        React-redux, REST Api, Storybook, HTML / CSS, Jest. <br/><br/>
                        Today implemented in this project: authorization, user subscription, pagination, editable
                        profile and validation forms.
                    </Project>
                    <Project href={"https://github.com/sibiryakov090591/TodoList"} imgUrl={[todoImg1, todoImg2]} title="Todo list">
                        What tools were used: React.js, Hooks, Redux, REST Api, Storybook, Snapshot, Material ui,
                        Jest. <br/><br/>
                        Today implemented in this project: adding lists, adding tasks to lists, validation forms,
                        filtration tasks, checking tasks.
                    </Project>
                    <Project href={"https://github.com/sibiryakov090591/counter-test-work"} imgUrl={[counterImg1, counterImg2, counterImg3]} title="Counter">
                        What tools were used: React.js, Hooks, Redux, HTML / CSS, Jest. <br/><br/>
                        Today implemented in this project: setting start value and end value and checking for correct
                        entry of values.
                    </Project>
                </div>
            </div>
        </section>
    )
}

export default Projects;
